

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter, Route } from 'react-router-dom'
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup'

import TouchBox from 'Components/touchBox/touchBox'
import TouchMenu from 'Components/touchBox/touchMenu'
import TouchContent from 'Components/touchBox/touchContent'
import TouchY from 'Components/touchBox/touchY'

import Footer from '../layout/footer/footer';
import style from './news.scss';

import News_1 from './pages/news_1';
import News_2 from './pages/news_2';
import NewDetail from './pages/NewDetail';

class NewsHome extends Component {
    constructor(props) {
        super(props);
    }
    touchEnd(cur) {
        this.props.dispatch('news/save', { cur })
    }
    render() {
        let { cur, news, menu } = this.props
        return (
            <div className='warper'>
                <div className="header">
                    <div className="h1">新闻</div>
                </div>
                <div className="content">
                    <div className={style.content}>
                        {menu.size > 0 && <TouchMenu onTouchEnd={this.touchEnd.bind(this)} cur={cur} menu={menu} />}
                        <TouchContent onTouchEnd={this.touchEnd.bind(this)} cur={cur} >
                            <News_1 index={0} cur={cur}></News_1>
                            <News_2 index={1} cur={cur}></News_2>
                            <TouchY>
                                <img src="/public/images/5.jpg" alt="" />
                                <div>1111111111111wwwww2w</div>
                                <div>1111111111111</div>
                                <div>343434</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                                <div>1111111111111</div>
                            </TouchY>
                            <TouchY>55555555555555555</TouchY>
                            <TouchY>55555555555555555</TouchY>
                            <TouchY>6666666666666</TouchY>
                            <TouchY>777777777777</TouchY>
                            <TouchY>8888888888888888</TouchY>
                        </TouchContent>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}

const News = connect(state => ({
    cur: state.getIn(['news', 'cur']) || 0,
    news: state.get('news'),
    menu: state.getIn(['init', 'menu']),
}))(NewsHome)

class NewsRoutes extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        let { match, location } = this.props
        let key = location.pathname
        if (location.pathname.indexOf('/news/') != -1) {
            key = '/news/:id'
        }
        return (
            <CSSTransitionGroup
                transitionName={g.trcls || 'fade'}
                transitionEnter={true}
                transitionLeave={true}
                transitionEnterTimeout={300}
                transitionLeaveTimeout={300}
                component="div">
                <div className='fade' key={key}>
                    <Route exact location={location} path='/news' component={News} />
                    <Route location={location} path={'/news/:id'} component={NewDetail} />
                </div>
            </CSSTransitionGroup>
        )
    }
}

export default connect()(NewsRoutes)


