import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'

import { fromJS, Map, Seq, toKeyedSeq, Collection, Range } from 'immutable';

import TouchY from 'Components/touchBox/touchY'
import style from './news_1.scss';

class News_1 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isloading: true
        }
        this.loadmoreaction = this.loadmoreaction.bind(this)
        this.onScroll = this._onScroll.bind(this)
    }
    componentDidMount() {
        this._req(this.props)
    }
    shouldComponentUpdate(nextProps, nextState) {
        let { index, cur } = nextProps
        if (index == cur) {
            return true
        }
        return false
    }
    _onScroll(top) {
        let { index, dispatch } = this.props
        dispatch('news/setIn', { act: [index, 'scrollTop'], value: top })
    }
    async _req(props) {
        let { index, news, cur } = props
        let { isloading } = this.state
        let isloaded = news.getIn([index, 'isloaded'])
        if (isloading && isloaded) {
            this.setState({
                isloading: false
            })
        }
        if (index !== cur || isloaded) {
            return
        }
        let params = {
            page: 1,
            limit: 10
        }
        let res = await request('https://cnodejs.org/api/v1/topics', {
            params
        })
        if (res.success) {
            let list = {
                ...params,
                isloaded: true,
                data: res.data,
            }
            this.props.dispatch('news/setIn', { act: [index], value: list })
        }
        this.setState({
            isloading: false
        })
    }
    async loadmoreaction() {
        let { news, index } = this.props
        let { isloading } = this.state
        let curnews = news.get(index)
        let page = curnews.get('page')
        if (isloading) {
            return
        }
        this.setState({
            isloading: true
        })
        page++
        let res = await request('https://cnodejs.org/api/v1/topics', {
            params: {
                page: page,
                limit: 10
            }
        })
        if (res.success) {
            let data = curnews.get('data')
            data = data.concat(fromJS(res.data))
            this.props.dispatch('news/setIn', { act: [index, 'data'], value: data })
            this.props.dispatch('news/setIn', { act: [index, 'page'], value: page })
        }
        this.setState({
            isloading: false
        })
    }
    componentWillUnmount() {
        this.setState({
            isloading: false
        })
    }
    render() {
        let { news, index, dispatch } = this.props
        let { isloading } = this.state
        let data = news.getIn([index, 'data'])
        let scrollTop = news.getIn([index, 'scrollTop'])
        console.log(data&&data.toJS());
        
        return (
            <TouchY onScroll={this.onScroll} scrolltop={scrollTop}>
                <div className={style.lists}>
                    {data && data.map((d, i) => (
                        <Link key={i} to={{ pathname: '/news/' + d.get('id'), state: { urlback: 1 } }} onClick={() => dispatch('news/save', { curitem: d })} className={style.list}>
                            {d.getIn(['author', 'avatar_url']) && <div className={style.image} style={{
                                backgroundImage: 'url(' + d.getIn(['author', 'avatar_url']) + ')'
                            }}></div>}
                            <div className={style.dr}>
                                <div className={style.title}>{d.get('title')}</div>
                                <div className={style.time}>{d.get('create_at')}</div>
                            </div>
                        </Link>
                    ))}
                </div>
                {isloading ?
                    <div className='nodata'>数据正在快马加鞭赶来中...</div> :
                    <div onClick={this.loadmoreaction} className={style.loadmore}>点击加载更多</div>}
            </TouchY>
        )
    }
}

export default connect(state => ({
    news: state.get('news')
}))(News_1)



