import { Route } from 'react-router-dom'
import style from './iframeRoute.scss';

import React from 'react';

const IframeRoute = ({ component: Component, ...rest }) => {
    return (
        <Route {...rest} render={(props) => {
            let { state } = props.location
            return (
                <div className={style.iframebox}>
                    {state && state.outlink ?
                        <Iframe {...state} /> :
                        <Component {...props} />
                    }
                </div>
            )
        }} />
    )
}

export default IframeRoute

const Iframe = (props) => (
    <div className="warper">
        <div className="header">
            <a onClick={g.goBack} className="span">
                <i className="i iconfont icon-jiantou-left"></i>
            </a>
            <h1 className="h1">{props.outlinktitle || '外链'}</h1>
            <a className="span"></a>
        </div>
        <iframe className={'content ' + style.iframe} src={props.outlink || 'http://koa.bootcss.com/'}></iframe>
    </div>
)


