import React from 'react';
import { connect } from 'react-redux'
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup'
import IframeRoute from 'Components/iframe/iframeRoute'
import { Link } from 'react-router-dom'

import style from './NewDetail.scss';

class NewDetail extends React.Component {
    constructor(props) {
        super(props)
    }
    async componentDidMount() {
        let { match, curitem, dispatch } = this.props
        // if (curitem) {
        //     return
        // }
        let res = await request('https://cnodejs.org/api/v1/topic/' + match.params.id)
        if (res.success) {
            dispatch('news/save', { curitem: res.data })
            // dispatch('news/setIn', { act: ['curitem', 'replies'], value: res.data.replies })
        }
    }
    addlove(storeid) {
        console.log(storeid);
    }
    render() {
        let { curitem, match, location, history } = this.props
        console.log(location.state);
        return (
            <CSSTransitionGroup
                transitionName={g.trcls || 'fade'}
                transitionEnter={true}
                transitionLeave={true}
                transitionEnterTimeout={300}
                transitionLeaveTimeout={300}
                component="div">
                <div className='warper'>
                    <div className="header">
                        <a onClick={g.goBack} className="span">
                            <i className="i iconfont icon-jiantou-left"></i>
                        </a>
                        <div className="h1">文章详情</div>
                        <Link to={`${match.url}/comment`} className="span">
                            <i className="i iconfont icon-pinglun"></i>
                        </Link>
                    </div>
                    <div className="content">
                        {curitem ? <div className={style.detail}>
                            <div className={style.title}>{curitem.get('title')}</div>
                            <div ref={style.content} className={style.content} dangerouslySetInnerHTML={{ __html: curitem.get('content') || '数据正在加载中...' }}></div>
                        </div> : null}
                    </div>
                    {curitem ?
                        <div className={style.footer}>
                            <span onClick={this.addlove.bind(this, curitem.get('storeid'))} className={style.love}>
                                <i className={"iconfont icon-xin " + style.i}></i>
                                <span className={style.view}>{curitem.get('visit_count')}</span>
                            </span>
                            <Link to={`${match.url}/comment`} className={style.lk}>
                                <i className={"iconfont icon-pinglun " + style.i}></i>
                                <span className={style.view}>{curitem.get('reply_count')}</span>
                            </Link>
                        </div> :
                        null}
                </div>
                <div className='fade' key={location.pathname}>
                    <IframeRoute path={`${match.url}/comment`} location={location} component={CommentBox} />
                </div>
            </CSSTransitionGroup>
        )
    }
}

export default connect(state => ({
    curitem: state.getIn(['news', 'curitem']),
}))(NewDetail)

const Comment = ({ replies }) => {
    return (
        <div className='warper'>
            <div className="header">
                <a onClick={g.goBack} className="span">
                    <i className="i iconfont icon-jiantou-left"></i>
                </a>
                <div className="h1">文章评论</div>
                <a className="span"></a>
            </div>
            <div className="content">
                {replies ?
                    replies.map((d, i) => {
                        return (
                            <div className={style.comment} key={i}>
                                <Link to={'/user'} className={style.avatar}>
                                    <img src={d.getIn(['author', 'avatar_url'])} alt="" className={style.avatar_url} />
                                </Link>
                                <div className={style.rd}>
                                    <div className={style.name}>{d.getIn(['author', 'loginname'])}</div>
                                    <div className={style.content} dangerouslySetInnerHTML={{ __html: d.get('content') }}></div>
                                    <div className={style.time}>{d.get('create_at')}</div>
                                </div>
                            </div>
                        )
                    }) :
                    <div className='nodata'>暂无评论</div>
                }
            </div>
        </div>
    )
}

const CommentBox = connect(state => ({
    replies: state.getIn(['news', 'curitem', 'replies'])
}))(Comment)


