import style from './touchY.scss';
import React from 'react';

class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isloading: false
        }
        this.moveXY = 0
        this._onTouchStart = this._onTouchStart.bind(this)
        this._onTouchMove = this._onTouchMove.bind(this)
        this._onTouchEnd = this._onTouchEnd.bind(this)
        this._onScroll = this._onScroll.bind(this)
    }
    componentDidMount() {
        this._warp = this.refs.warp
        this._loading = this.refs.loading
        this._width = this._warp.getBoundingClientRect().width;
        this._warp.scrollTop = this.props.scrolltop || 0
    }
    //滑动效果
    _translate(attr, dist, speed) {
        this._loading.style.webkitTransitionDuration = this._loading.style.MozTransitionDuration =
            this._loading.style.msTransitionDuration = this._loading.style.OTransitionDuration =
            this._loading.style.transitionDuration = speed + 'ms';
        this._loading.style[attr] = dist;
    }
    _onTouchStart(e) {
        let point = e.touches[0] || e;
        this._startX = point.pageX;
        this._startY = point.pageY;
    }
    _onTouchMove(e) {
        // if (!this.props.touchX && !this.props.touchY) {
        //     return
        // }
        let point = e.touches[0] || e;
        let moveX = point.pageX - this._startX;
        this._moveY = point.pageY - this._startY;
        if (this.moveXY == 0) {
            if (Math.abs(moveX) > Math.abs(this._moveY)) {
                this.moveXY = 1

            } else {
                this.moveXY = 2
            }
        }
        if (Math.abs(moveX) > Math.abs(this._moveY) || this.moveXY == 1) {
            return
        } else {
            // (e.stopPropagation && e.stopPropagation(), 1) || (e.cancelBubble = true);
            if (this._warp.scrollTop != 0) {
                return
            }
            this.props.touchY && this.props.touchY(this._moveY)
            if (this._moveY > 20) {
                this._translate('top', '5rem', 0)
                let v = this._moveY / 50 > 1 ? 1 : this._moveY / 50
                this._translate('margin-top', 2 * Math.sin(this._moveY / 180) + 'rem', 0)
                this._translate('opacity', v, 0)
            }
        }
    }
    _onTouchEnd(e) {
        this.moveXY = 0
        this._translate('margin-top', 0, 200)
        if (!this.props.touchEnd || this._moveY < 50) {
            this._translate('top', '-5rem', 0)
        } else {
            this.setState({
                isloading: true
            })
            this.props.touchEnd()
        }
        this._moveY = 0
    }
    _onScroll(e) {
        this.props.onScroll && this.props.onScroll(this._warp.scrollTop)
    }
    render() {
        let { children, className = '' } = this.props
        let { isloading } = this.state
        let loadingstyle = {
            top: isloading ? '5rem' : '-5rem',
            transitionDuration: '0s',
        }
        let isloadingclass = isloading ? style.loading2 + ' ' + style.isloading : style.loading2
        return (
            <div className={style.content + ' ' + className}>
                <div className={isloadingclass} style={loadingstyle} ref='loading'>
                    <div className={style.ballcliprotatepulse}>
                        <div></div>
                        <div></div>
                    </div>
                </div>
                <div className={style.warp}
                    ref='warp'
                    id='warp'
                    onTouchStart={this._onTouchStart}
                    onTouchMove={this._onTouchMove}
                    onTouchEnd={this._onTouchEnd}
                    onScroll={this._onScroll}
                >{children}</div>
            </div>
        )
    }
}

export default Content


