import style from './touchMenu.scss';
import React from 'react';

class TouchMenu extends React.Component {
    constructor(props) {
        super(props);
        this._moveX = 0
        this._moveS = 0
        this._onTouchStart = this._onTouchStart.bind(this)
        this._onTouchMove = this._onTouchMove.bind(this)
        this._onTouchEnd = this._onTouchEnd.bind(this)
    }
    componentDidMount() {
        this._trans(0)
        this._warper = this.refs.tabwarp
        this._width = this._warper.getBoundingClientRect().width;
    }
    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.cur == this.props.cur && nextProps.menu == this.props.menu) {
            return false
        }
        return true
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.cur != this.props.cur) {
            this._trans()
        }
    }
    _trans(speed = 200) {
        let { offsetWidth: width, scrollWidth: sw } = this.refs.tabwarp
        let { offsetLeft, offsetWidth } = this.refs.curtab
        let mx = width / 2 - offsetLeft - offsetWidth / 2
        if (mx > 0 || width > sw) {
            mx = 0
        } else if (width - sw > mx) {
            mx = width - sw
        }
        this._translate(mx, speed)
    }
    _translate(dist, speed) {
        let tabwarp = this.refs.tabwarp
        tabwarp.style.webkitTransitionDuration = tabwarp.style.MozTransitionDuration =
            tabwarp.style.msTransitionDuration = tabwarp.style.OTransitionDuration =
            tabwarp.style.transitionDuration = speed + 'ms';
        tabwarp.style.transform = 'translateX(' + dist + 'px)';
    }
    _onClick(cur) {
        this.props.onTouchEnd && this.props.onTouchEnd(cur)
    }
    _onTouchStart(e) {
        let point = e.touches[0] || e;
        this._startX = point.pageX;
        let transformX = this._warper.style.transform
        this._startX2 = parseInt(transformX.substring(11, transformX.length - 3))
    }
    _onTouchMove(e) {
        let point = e.touches[0] || e;
        this._moveX = point.pageX - this._startX;
        this._moveS = 0.6 * this._moveX + this._startX2
        this._translate(this._moveS, 0)
    }
    _onTouchEnd(e) {
        let { offsetWidth: width, scrollWidth: sw } = this.refs.tabwarp
        let sx = -(sw - width)
        this._moveX = 0
        if (this._moveS > 0) {
            this._translate(0, 200)
        } else if (this._moveS < sx) {
            this._translate(sx, 200)
        }
    }
    render() {
        let { cur, menu } = this.props
        return (
            <div className={style.tab} ref='tab'>
                <div className={style.tabwarp} ref='tabwarp'
                    onTouchStart={this._onTouchStart}
                    onTouchMove={this._onTouchMove}
                    onTouchEnd={this._onTouchEnd}>
                    <div className={style.tabwarper} ref='tabwarper'>
                        {menu.map((d, i) => {
                            if (cur == i) {
                                return <div className={style.active} ref='curtab' key={i}>{d}</div>
                            }
                            return <div className={style.a} key={i} onClick={this._onClick.bind(this, i)}>{d}</div>
                        })}
                    </div>
                </div>
            </div>
        )
    }
}

export default TouchMenu


