import { connect } from 'react-redux'
import style from './touchBox.scss';

import React from 'react';

import TouchY from './touchY';
import TouchContent from './touchContent';
import TouchMenu from './touchMenu';

const menu = ['菜单1', '菜单2', '菜单3', '菜单4', '菜单5', '菜单6', '菜单7', '菜单8']

class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cur: 6
        }
    }
    touchEnd(cur) {
        this.setState({ cur: cur })
    }
    render() {
        let { children } = this.props
        let { cur } = this.state
        return (
            <div className={style.content}>
                <TouchMenu onTouchEnd={this.touchEnd.bind(this)} cur={cur} menu={menu} />
                 <TouchContent onTouchEnd={this.touchEnd.bind(this)} cur={cur} >
                    <TouchY>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>343434</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                        <div>1111111111111</div>
                    </TouchY>
                    <TouchY>22222222222222</TouchY>
                    <TouchY>333333333333</TouchY>
                    <TouchY>444444444444444</TouchY>
                    <TouchY>55555555555555555</TouchY>
                    <TouchY>6666666666666</TouchY>
                    <TouchY>777777777777</TouchY>
                    <TouchY>8888888888888888</TouchY>
                </TouchContent> 
            </div>
        )
    }
}

export default connect((state) => ({ isloading: state.getIn(['init', 'isloading']) }))(Content)


